import React, {useEffect, useState} from 'react';
import {
  Button,
  Card,
  Table,
  Flex,
  Input,
  Form,
  Menu,
  Row,
  Col,
  Divider, Modal, message,
} from 'antd';
import {DeleteOutlined, EyeOutlined, PlusOutlined} from '@ant-design/icons';
import moment from 'moment';
import EllipsisDropdown from '../../../../../components/shared-components/EllipsisDropdown';
import GroupKeycloakService from '../../../../../services/keycloak/group.keycloak.service';

export const Page = () => {
  const [open, setOpen] = useState(false);
  const [form] = Form.useForm();

  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const [params, setParams] = useState({
    page: 1,
    limit: 10,
  });
  const [loadings, setLoadings] = useState([]);

  /**
   * @param {string} id
   * @param {number} index
   * @private
   */
  function _onDelete(id, index) {
    setIndexLoading(index, true);
    message.loading({
      key: 'delete-item',
      content: 'Loading deleted item',
      duration: 10,
    })
      .then(r => r);

    new GroupKeycloakService()
      .delete(id)
      .then(() => {
        setData(prevState => [
          ...prevState.map(child => ({
            ...child,
            deletedAt: id === child?.id ? moment() : null,
          })),
        ]);
        setTimeout(() => {
          setIndexLoading(index, false);
          setData(prevState => [...prevState.filter(child => child?.id !== id)]);
          message
            .success({
              key: 'delete-item',
              content: 'Successfully deleted item',
              duration: 4,
            })
            .then(r => r);
        }, 1000);
      })
      .catch((err) => {
        setIndexLoading(index, false);
        message
          .success({
            key: 'delete-item',
            content: err?.message ?? err?.errorMessage ?? 'Error while deleting item',
            duration: 4,
          })
          .then(r => r);
      });
  }

  /**
   * @param {number} index
   * @param {boolean} value
   */
  const setIndexLoading = (index, value = true) => {
    setLoadings(prevLoadings => {
      prevLoadings[index] = value;
      return prevLoadings ?? [];
    });
  };

  function _onCreate() {
    form.validateFields()
      .then((values) => {
        setLoading(true);
        message.loading({
          key: 'save',
          content: 'Loading...',
          duration: 10,
        }).then(r => r);

        new GroupKeycloakService()
          .store(values)
          .then(() => {
            message.success({
              key: 'save',
              content: 'Successfully created',
              duration: 4,
            }).then(r => r);
            setParams((prevState) => ({...prevState}));
            form.resetFields();
            setLoading(false);
            setOpen(false);
          })
          .catch((err) => {
            message.error({
              key: 'save',
              content: err?.message ?? err?.errorMessage ?? 'Error creating group',
              duration: 4,
            }).then(r => r);
            setLoading(false);
          });
      });
  }

  const columns = [
    {
      visible: true,
      width: 'calc((100%/5)-100px)',
      key: 'name',
      title: 'Name',
      dataIndex: 'name',
    },
    {
      visible: true,
      width: 'calc((100%/5)-100px)',
      key: 'path',
      title: 'Path',
      dataIndex: 'path',
    },
    {
      visible: true,
      width: 'calc((100%/5)-100px)',
      key: 'subGroups',
      title: 'Sub Group',
      dataIndex: 'subGroups',
      render(_) {
        return Array.isArray(_) && _.length > 0 ? _.length : _;
      }
    },
    {
      visible: true,
      width: 'calc((100%/5)-100px)',
      key: 'subGroupCount',
      title: 'Sub Group Count',
      dataIndex: 'subGroupCount',
    },
    {
      visible: true,
      dataIndex: 'id',
      width: '140px',
      title: 'Action',
      render: (_, val, index) => {
        return (
          <Flex gap={10} align="center">
            <Button
              loading={loadings[index] ?? val?.deletedAt ?? false}
              size="small"
              danger
              shape="circle"
              ghost
              onClick={() => {
                _onDelete(_, index);
              }}
              icon={<DeleteOutlined />}
            />
            <EllipsisDropdown
              placement="left"
              menu={
                <Menu>
                  <Menu.Item
                    icon={<EyeOutlined/>}
                    disabled={Array.isArray(loadings) && loadings.length > 0 && loadings.some(item => item === true)}
                  >
                    Detail
                  </Menu.Item>
                  <Divider className="m-0"/>
                  <Menu.Item
                    disabled={Array.isArray(loadings) && loadings.length > 0 && loadings.some(item => item === true)}
                    onClick={() => {
                      _onDelete(_, index);
                    }}
                    icon={<DeleteOutlined/>}
                  >
                    Delete
                  </Menu.Item>
                </Menu>
              }
            />
          </Flex>
        );
      },
    },
  ];

  useEffect(() => {
    setLoading(true);
    new GroupKeycloakService()
      .get(params)
      .then((response) => {
        setData(Array.isArray(response) ? response : []);
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
        setData([]);
      });
  }, [params]);

  return (
    <React.Fragment>
      <Card>
        <Flex justify="space-between" className="mb-4">
          <div/>
          <div>
            <Button
              disabled={Array.isArray(loadings) && loadings.length > 0 && loadings.some(item => item === true)}
              icon={<PlusOutlined/>}
              type="primary"
              onClick={() => setOpen(true)}
            >
              Create
            </Button>
          </div>
        </Flex>
        <Row gutter={[24, 24]}>
          <Col xs={24}>
            <Table
              scroll={{
                // y: 50 * 9,
                x: 768,
              }}
              rowClassName={record => (record?.deletedAt ? '!bg-red-500 !bg-opacity-20' : '')}
              loading={loading}
              columns={columns}
              dataSource={data}
            />
          </Col>
        </Row>
      </Card>
      <Modal open={open} onCancel={() => setOpen(false)} onOk={_onCreate}>
        <Form form={form} layout="vertical">
          <Form.Item name="name" label="Name" rules={[{required: true}]}>
            <Input placeholder="Input your name"/>
          </Form.Item>
        </Form>
      </Modal>
    </React.Fragment>
  );
};

export default Page;
